* {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

.brand {
  text-decoration: none !important;
  color: #fff !important;
}

.home {
  display: flex;
}

.productContainer {
  display: flex;
  flex-wrap: wrap;
  width: 78%;
  justify-content: space-around;
  padding: 20px;
}

.filters {
  background-color: #343434;
  color: #fff;
  padding: 20px;
  display: flex;
  flex-direction: column;
  width: 20%;
  margin: 10px;
  height: 84vh;
}

.filters>span {
  padding-bottom: 20px
}

.title {
  font-size: 30px;
}

.products {
  width: 30%;
  margin: 10px;
}

.cartItem {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0 20px;
  margin-bottom: 20px;
}

.cartItemImg {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  object-fit: cover;
}

.cartItemDetail {
  display: flex;
  flex: 1 1;
  padding: 0 20px;
  flex-direction: column;
}

.summary {
  width: 30%;
}

.search-box {
  width: 400px !important;
}

@media (max-width: 771px) {
  .productContainer {
    width: 58%;
    flex-direction: column;
  }

  .filters {
    width: 40%;
    padding: 10px;
    margin: 5;
  }

  .products {
    width: 100%;
  }

  .filters>span {
    font-size: 13px;
  }

  .title {
    font-size: 25px;
  }

  .search-box {
    width: auto !important;
  }

}
